body {
  margin: 0;
  padding: 0;
  font-family: Futura, sans-serif;
  color: #666;
  color: var(--secondary-text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

:root {
  --active-tab-color: #108adc;/*#3E78B2;*/
  --not-active-tab-color: #9c9c9c;

  --border-color: #eaeaea;/*#aaa;*/
  --primary-text-color: #333;
  --secondary-text-color: #666;

  --connection-ok-color: #108fe4;
  --connection-not-ok-color: #ff3a30;

  --btn-color: #108fe4;

  --search-input-background: #f4f4f4;
  --search-input-text-color: #a0a0a0;

  --columns-height: 100%;
  --left-column-max-width: 300px;

  --bar-height: 50px;

}

.dragger {
  -webkit-app-region: drag;
  width: 100vw;
  height: 22px;
  background: #eaeaea;
  background: var(--border-color);
  opacity: 1;
  /*position: absolute;*/
  /*z-index: 100;*/
  -moz-user-select: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

a:active, a {
  color: #f4f4f4;
  color: var(--search-input-background);
  text-decoration: none;
}



.left-column {
    height: 100vh;
    overflow-y: hidden;
    overflow-x: hidden;

    border-right: 1px solid var(--border-color);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-basis: var(--left-column-max-width);
            flex-basis: var(--left-column-max-width);
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    box-sizing: border-box;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
}
.dialog-list {
    overflow-y: scroll;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    width: var(--left-column-max-width);
}
.dialog-list__dialog-mini {
    width: var(--left-column-max-width);

    height: 60px;
    padding: 10px;
    border-bottom: 1px solid var(--border-color);
    border-top: none;
    font-family: sans-serif, Futura;
    transition: background 0.2s ease-out;
    box-sizing: border-box;
}

.dialog-list__dialog-mini:hover {
    background: #FCF6BD;
}

.dialog-list__dialog-mini--checked {
    background: #E7BB41;
}


.dialog-mini__author {
